import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { NotificationServiceService } from 'src/@core/Models/Notification/notification-service.service';
import { EmployeesLookupComponent } from 'src/app/erp-hr/hr-lookups/configurations-lookups/employees-lookup/employees-lookup.component';
import { FilesService } from 'src/app/shared/services/files.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { AllEmployeesComponent } from '../../../employees/all-employees/all-employees.component';
import { SharedDocumentService } from 'src/app/erp-hr/data/shared-documents/shared-documents.service';

@Component({
  selector: 'app-manage-shared-documents',
  templateUrl: './manage-shared-documents.component.html',
  styleUrls: ['./manage-shared-documents.component.sass']
})
export class ManageSharedDocumentsComponent implements OnInit {

  mngForm: FormGroup;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;
  isLoading: boolean = true;

  documentTypeArray: string[] = [
    "Pre-Onboarding Document",
    "Workplace safety guidelines",
    "Employee Document",
    "HR policies",
    "Exit Documents",
    "Corporate news",
    "Case management",
    "Training materials",
    "Employee handbook",
    "LeaveDocument",
    "Benefits information",
    "Pay stubs",
    "Others",
  ];

  constructor(
    public dialogRef: MatDialogRef<AllEmployeesComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private sharedDocumentService: SharedDocumentService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private notificationAPI: NotificationServiceService
  ) {}

  ngOnInit(): void {
    console.log("LOG DATA = ", this.data);
    this.pageFunction = this.data.action;
    this.formData = this.data.record;
    this.generateForm(this.formData);

    this.getPage();
  }
  ngAfterViewInit() {
    if (this.data.action !== "Add") {
      this.generateForm(this.formData);
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  disableTransactionType: boolean = true;
  getPage() {
    this.disableTransactionType = true;
    console.log("this.data.action :", this.data.action);
    if (this.data.action === "Add") {
      this.isLoading = false;
      this.generateForm(this.data.record);
    } else if (this.data.action === "Update") {
      this.generateForm(this.data.record);
    } else if (this.data.action === "View") {
      this.generateForm(this.data.record);
      this.activateViewMode();
    }
    this.isLoading = false;
  }

  formData: any;
  generateForm(formData?): void {
    console.log("generateForm formData :: ", formData);
    this.mngForm = this.fb.group({
      id: [formData?.id ?? ""],
      fileName: [formData?.fileName ?? "", Validators.required],
      fileExtension: [formData?.fileExtension ?? "", Validators.required],
      documentFile: [formData?.documentFile ?? "", Validators.required],

      documentType: [formData?.documentType ?? "", Validators.required],
      documentTitle: [formData?.documentTitle ?? "", Validators.required],

    });
  }
  
  viewMode: boolean = false;
  activateViewMode() {
    this.viewMode = true;
  }

  //********************************************************************************************************************** */
  //********************************************************************************************************************** */
  file_name: any;
  base64File: string;
  loading: boolean = false;
  handleFileInput(event: any) {
    const file: File = event.target.files[0];
    if (file) {
      this.file_name =file.name
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.mngForm.patchValue({
          fileName: file.name,
          fileExtension: file.name.split(".").pop(),
          documentFile: reader.result,
        });
      };
    }
  }
  getDocument() {
    this.loading = true;
    let params = {
      id: this.data.record.id,
    };
    this.sharedDocumentService.findById(params).subscribe(
      (res) => {
        console.log(res);
        if (res.entity.attachment) {
          this.loading = false;
          this.downloadDocument(res.entity.attachment);
        } else {
          this.loading = false;
          this.notificationAPI.alertWarning("No Document Attached!");
        }
      },
      (err) => {
        this.loading = false;
      }
    );
  }

  downloadDocument(fileToDownload: string) {
    let fileName = "LeaveForm";

    fetch(fileToDownload)
      .then((response) => response.blob())
      .then((blob) => {
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", `${fileName}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      });
  }

  //********************************************************************************************************************** */

  //********************************************************************************************************************** */

  posting: boolean = false;

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);
    this.posting = true;
    if (this.pageFunction === "Add") {
      this.sharedDocumentService
        .create(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("res: ", res);
            if (res.statusCode == 201) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    } else if (this.pageFunction === "Update") {
      this.sharedDocumentService
        .update(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            this.response = res;
            console.log("resres:: ", res);
            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {
            this.posting = false;
            setTimeout(() => {
              this.onNoClick();
            }, 3000);
          },
        }),
        Subscription;
    }
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }

  selectedItem: any;
  employeeLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "SingleEmployee",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(EmployeesLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((res) => {
      if (res && res.data && res.data.length > 0) {
        this.mngForm.patchValue({
          empPf: res.data[0].empNo,
        });
      }
    });
  }


}
